
.headingCtn {

    background-color: transparent;
    width: 100%;
    border-width: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-top: 3rem;
}


.getText {
    font-size: 15px;
    color: white;
    font-weight: 400;
}

#aboutme {

    font-size: 24px;
    color:aqua;
    font-weight: 600;
    font-family: 'Poppins';
}

.award_container {
    display: grid;
    grid-template-columns: repeat(2 , 1fr);
    gap:1rem;
    width: 80%;

}


.portfolio_item {
    background:transparent;
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid grey;
    transition: ease-in-out 400ms;

}

.portfolio_item:hover{

    border-color: darkgreen;
    background:powderblue;
}

.portfolio_item-image {

border-radius: 1.5rem;
overflow: hidden;
height: 20rem;
width: auto;
}

.portfolio_item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio_item-cta{

    display: flex;
    gap: 1rem;
    margin-bottom: 1.2rem;
}

img{
    width: 100%;
    object-fit: contain;
}


.buttonCtn {

    border-radius: 5px;
    border-width: 20px;
    background-color:transparent;
    border: 1.5px solid #00bfff;
    height: 6vh;
    width: 10vw;
    display: flex;
    justify-content: center;
    align-items: center;

}
.buttonCtn:hover {


    border-radius: 5px;
    border-width: 20px;
    background-color:  #00bfff;
